        display:block;
        height:10.6875rem;
}
.indexBanner .prev, .indexBanner .next{
        display:none;
}
.indexBanner.tag{
        position:absolute;
        bottom:0.76875rem;
        left:0;
        width:100%;
        text-align: center;
}
